import { createSlice } from '@reduxjs/toolkit';
import { pathItemType } from './../models/models';


const initialState = {
    path: [
        {text: '', moveTo: '../findcourse'},
        {text: '  ', moveTo: '../stocks'},
    ] as pathItemType[],
    header: {
        heading: ' -',
        text: '-             .     -,     .'
    }
}

export const StocksReducer = createSlice({
    name: 'stock',
    initialState,
    reducers: {

    }
})

export default StocksReducer.reducer